<template>
  <div>
    <vue-cal :disable-views="['years', 'year', 'month']" :time-from="9 * 60" :time-to="20 * 60" :time-step="15"
      :hide-weekdays="[1, 2, 3, 7]" :split-days="eventRooms" show-time-in-cells :events="events"
      class="vuecal--full-height-delete" locale="es" active-view="day" :on-event-click="onEventClick">
      <template #event="{ event }">
        <div class="vuecal__event-title" v-html="event.title" />

        <small class="vuecal__event-time">{{ event.start &&
          event.start.formatTime() }} - {{ event.end && event.end.formatTime() }} ({{
    event.eventData.duration }})
        </small>
      </template>
    </vue-cal>
    <v-dialog v-model="showDialog">
      <v-card>
        <v-card-title>
          <span>{{ selectedEvent.title }}</span>
          <v-spacer />
          <strong>{{ selectedEvent.start && selectedEvent.start.format('DD/MM/YYYY') }} - {{ selectedEvent.start &&
            selectedEvent.start.formatTime() }} - {{ selectedEvent.end && selectedEvent.end.formatTime() }} ({{
    selectedEvent.eventData.duration }})</strong>
          <v-spacer />
          <strong>{{ selectedEvent.eventData.room.label }}</strong>
        </v-card-title>
        <v-card-text>
          <p v-html="selectedEvent.eventData.description" />

        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import VueCal from 'vue-cal'
import 'vue-cal/dist/vuecal.css'
// import vueCalEventsPeople from '../../../myEvents.json'
import vueCalEventsPeople from '../../../vueCalEventsPeople.json'
import vueCalEventRooms from '../../../vueCalEventRooms.json'
export default {
  name: 'HelloWorld',
  components: { VueCal },
  props: {
    msg: String
  },
  data: function () {
    return {
      events: vueCalEventsPeople,
      eventRooms: vueCalEventRooms,
      selectedEvent: {},
      showDialog: false
    }
  },
  methods: {
    onEventClick(event, e) {
      this.selectedEvent = event
      this.showDialog = true

      // Prevent navigating to narrower view (default vue-cal behavior).
      e.stopPropagation()
    }
  }
}
</script>

